// Menu

.collapsible-menu {

  button {
    outline: none;
  }

  .collapsible-menu-toggle {
    display: none;

    @include breakpoint(map-get($breakpoints, small)) {
      display: block;
    }
  }

  .collapsible-menu-items {
    li {
      display: inline-block;
      margin-right: 25px;
      font-size: $base-font-size * 0.9;

      a {
        color: $color-text;

        &:hover {
          color: $action-color;
        }
      }

      &.active a {
        color: $action-color;
      }
    }

    @include breakpoint(map-get($breakpoints, small)) {
      display: none;

      margin-left: 0;
      padding-left: 0;

      li {
        display: block;
      }
    }
  }
}

// Cards


.status {
  @extend %card;
}


// Accepted status

.accepted-status {
  font-size: $font-size-small;
  display: inline-block;

  .accepted-label {
    background-color: $color-green;
    padding: 2px 22px 2px 10px;    
  }

  select {
    font-size: $font-size-small;

    &.pending {
      background-color: white;
      border: $base-border;    
    }

    &.rejected {
      background-color: white;
      color: $color-red-light;
      border: $base-border;
    }
  }
}

// Filters

.filter-group {
  margin: 20px 0px;
  
  .dropdown {
    display: inline-block;
    width: 200px;
    margin-right: 5px;
  }
}

